import React from 'react';
import Card from './Card';

const CardSection = (props) => (
    <div className="Wedding-card-section">
        <h3 className="Wedding-card-section-title">{ props.section.title }</h3>
        <div>
            {
                props.section.details.map((detail) => {
                    return <Card key={ detail.name } detail={ detail } />;
                })
            }
        </div>
    </div>
);

export default CardSection;



