import React from 'react';
import roche_harbor_2 from '../../images/roche_harbor_2.jpg';

const Hero = () => (
    <div className="Wedding-hero">
        <img src={ roche_harbor_2 } alt="roche harbor picture" />
        <div className="Wedding-text-overlay">
            <div className="Wedding-overlay large">Youre invited</div>
            <div className="Wedding-overlay small">to the</div>
            <div className="Wedding-overlay large">Young Wedding</div>
        </div>
    </div>
);

export default Hero;



