import React from 'react';
import { Link } from 'react-router-dom';

const NotFoundPage = () => (
    <div>
        <h3 className="wedding-not-found">Where are you going? Head back to the <Link to="/">site!</Link></h3>
    </div>
);

export default NotFoundPage;



