import React from 'react';

const Card = (props) => (
    <div className="Wedding-card">

        <div className="Wedding-card-name">{ props.detail.name }:</div>
        <div className="Wedding-card-description">{ props.detail.description }</div>
    
    </div>
);

export default Card;



